body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.fo-phone {
  display: block;
}

.fo-phone_number {
  font-weight: bold;
  text-decoration: none; /* no underline */
}

.fo-phone_number_wrapper {
  font-size: 20px;
}

.fo-phone_note {
  color: #838383;
}
@media screen and (min-width: 980px) {
}

@media screen and (min-width: 1045px) {
}

.fo-title {
  margin-bottom: 7px;
  font-size: 25px;
}

.fo-title-wrapper {
  display: block;
}

.fo-title-phone {
  display: block;
}

.fo-title-phone_number {
  font-weight: bold;
  text-decoration: none; /* no underline */
}

.fo-title-phone_number_wrapper {
  font-size: 20px;
}

.fo-title-phone_note {
  color: dimgray;
}
@media screen and (min-width: 980px) {
}

@media screen and (min-width: 1045px) {
  .fo-title-wrapper {
    display: grid;
    grid-template-columns: 15% 70%;
  }

  .fo-title {
    margin-top: 0;
  }
}

.fo-info-box_wrapper {
  margin: 20px 0;
}
.fo-info-box_title {
  margin: 0;
  color: #8e292d;
}

.fo-info-box_text {
  color: #838383;
  margin: 5px 0;
}

.fo-info-box_bullet-list {
  margin: 5px 0;
  padding-left: 20px;
  list-style: none;
  color: #838383;
}

.fo-info-box_bullet-point::before {
  content: "\2022";
  color: #5eafcd;
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1.2em;
}

.fo-about-us_info {
  display: block;
  margin: 10px 0;
}

@media screen and (min-width: 980px) {
  .fo-about-us_info {
    display: grid;
    grid-template-columns: 50% 50%;
  }
}

.fo-content-body {
  margin: 85px 7px;
  margin-bottom: 10px;
}

@media screen and (min-width: 754px) {
  .fo-content-body {
    margin: 64px 7px;
  }
}

@media screen and (min-width: 980px) {
  .fo-content-body {
    margin: 95px 7px;
  }
}
.fo-hamburger {
  width: 35px;
  height: 5px;
  background-color: white;
  margin: 6px 0;
}

.fo-close-menu-button {
  color: white;
  width: 35px;
  height: 39px;
  font-size: 35px;
  font-weight: bold;
}

.fo-menu-button {
  background-color: transparent;
  border: transparent;
  width: 38px;
  height: 73px;
}

.fo-menu-button:focus {
  outline: 0;
}
.fo-hamburger__clicked {
  background-color: grey !important;
}

@media screen and (min-width: 980px) {
  .fo-menu-button {
    display: none;
  }
}

@media screen and (min-width: 754px) {
  .fo-menu-button {
    height: 57px;
  }
}

.fo-menu {
  margin: 7px;
  padding: 0;
  display: none;
}

.fo-menu__open {
  display: block;
}

.fo-menu-link {
  display: inline-block;
  border: 1px solid #5eb2d1;
  width: 100%;
  color: #3fa3c9;
  text-align: center;
  font-weight: bold;
  font-size: 15px;
  background-color: white;
  margin: 2px 0;
  padding: 3px 0;
}

.fo-menu-link:visited {
  color: #3fa3c9;
}

.fo-menu-link:hover {
  color: #434242;
}

@media screen and (min-width: 980px) {
  .fo-menu {
    display: inline-block;
  }

  .fo-menu-link {
    width: auto;
    width: initial;
    margin: 0;
    margin-right: 10px;
    padding: 5px;
  }
}

@media screen and (min-width: 1045px) {
    .fo-menu-link {
        margin-right: 10px;
        padding: 10px 5px;
      }
    }

.fo-h1-wrapper {
  margin: 0;
  height: 76px;
  margin-right: 7px;
}
.fo-h1 {
  width: 100%;
  color: white;
  margin: 0;
  margin-top: 5px !important;
  display: block;
  font-size: 30px;
  text-align: right;
}

.fo-header {
  display: grid;
  grid-template-columns: 48% 52%;
}

.fo-header-container {
  width: 100%;
  background-color: #1b246f;
  position: fixed;
  top: 0;
  z-index: 1;
}

@media screen and (min-width: 754px) {
  .fo-h1 {
    text-align: left;
  }

  .fo-h1-wrapper {
    height: 60px;
  }
}

@media screen and (min-width: 980px) {
  .fo-header {
    display: inline-block;
  }

  .fo-h1-wrapper {
    margin-left: 7px;
    height: 40px;
  }
}


@media screen and (min-width: 1045px) {
  .fo-h1-wrapper {
    height: 76px;
  }
}

.fo-address-title {
  font-size: 20px;
  margin: 0;
}

.fo-address-line {
  display: block;
}

.fo-footer-divider {
  border: none;
  border-top: 0.5px dotted black;
  color: black;
  background-color: transparent;
  background-color: initial;
  height: 1px;
}

.fo-footer_wrapper {
  width: 100%;
  background-color: #f6f3f3;
  text-align: center;
  margin-top: 10px;
  padding-bottom: 10px;
}

.fo-footer-contact-us {
  font-size: 20px;
  margin: 0;
}

.fo-footer-social-link {
  margin-left: 20px;
}
.fo-footer-social {
  color: white;
  background-color: #1b246f;
  padding: 10px;
}

.fo-footer-social-link:visited {
  color: white;
}
.fo-footer-social-link:hover {
  color: lightblue;
}

.fo-footer-social-header {
  display: none;
}
.fo-footer-title {
  margin: 5px 0;
}

@media screen and (min-width: 980px) {
  .fo-footer-social-header {
    display: block;
    margin: 5px;
  }

  .fo-footer-social-link {
    display: inline-block;
  }

  .fo-footer-info {
    display: grid;
    grid-template-columns: 50% 50%;
    margin: 0 200px;
  }
  .fo-footer-divider {
    display: none;
  }
}

